export const move = (arr, removeFrom = null, insertAt = null) => {
    /*
        Adds item if removeFrom is null
        Removes item if insertAt is null
    */
    if (insertAt === removeFrom) return arr;
    if (isNaN(Number(removeFrom) || isNaN(Number(insertAt)))) return arr;

    let _arr = [...arr];

    const item = (removeFrom !== null) ? _arr.splice(removeFrom, 1)[0] : null;
    if (insertAt !== null) {
        _arr.splice(insertAt, 0, item);
    }

    return _arr;
};

export const shuffle = arr => arr
    .map(a => [Math.random(), a])
    .sort((a, b) => a[0] - b[0])
    .map(a => a[1]);

export const reverse = arr => arr.slice().reverse();

export function multiMove(array, indexes, toIndex) {
    const element = array[toIndex];
    const removedElements = array.filter((_, index) => indexes.includes(index));
    const remainingElements = array.filter((_, index) => !indexes.includes(index));
    console.table(removedElements);
    console.table(remainingElements);
    const index = remainingElements.indexOf(element);
    console.log(index);
    remainingElements.splice(index, 0, ...removedElements);
    console.table(remainingElements);
    return remainingElements;

}