const transitions = {
    grasp: 'box-shadow .2s ease-in-out, background-color .2s ease-in-out',
    moveIntoPlace: 'transform .2s ease-in-out',
    settleIntoPlace: 'box-shadow 0.4s ease-in-out, background-color 0.4s ease-in-out'
}


export default function elementCache(elements, props) {

    let staggerDelay = 0.02;
    const elementCache = elements.map(element => {
        // element.classList.add('fadein');
        element.style.transition = transitions.settleIntoPlace;
        element.style.transitionDelay = `${0.5 + staggerDelay}s`;
        element.classList.remove(props.dragClassName);
        element.classList.remove('shadow');
        element.style.willChange = null;
        staggerDelay += staggerDelay;
        return new Element(element)
    });

    return {

        get: index => elementCache[index],

        find: element => elementCache.find(({ element: cachedElement }) => cachedElement === element),

        get count() { return elementCache.length },

        removeAt(index) { elementCache.splice(index, 1) },

        resetStyles() {
            elementCache.forEach(({ element, selected }) => {
                if (selected) {
                    element.style.willChange = 'opacity';
                    element.classList.add('fadein');
                }
                else {
                    element.classList.remove('fadein');
                }

                element.style.transition = null;
                element.style.transform = null;
            });
        },

        getSelected: () => elementCache.filter(({ selected }) => selected),

        getSelectedIndexes() {
            let selectedIndexes = [];
            elementCache.forEach(({ element, selected }) => {
                const index = elements.indexOf(element);
                selected && index > -1 && selectedIndexes.push(index)
            });
            return selectedIndexes;
        },

        print() {
            console.table(elementCache.map(element => {
                const { element: { innerText: item }, top, translateY } = element;
                return { item, top, translateY }
            }));
        }
    }

    function Element(element) {
        const { offsetTop, offsetHeight: height, translateY = 0 } = element;
        return {
            element,
            offsetTop,
            height,
            translateY,
            selected: null,
            get top() { return this.offsetTop + this.translateY },
            select() {
                this.selected = true;
                element.style.transition = transitions.grasp;
                element.classList.add(props.dragClassName);
                props.raised && element.classList.add('shadow');

            },
            deselect() {
                this.selected = false;
                element.style.transition = transitions.settleIntoPlace;
                element.classList.remove(props.dragClassName);
                element.classList.remove('shadow');

            },
            toggle() {
                if (this.selected)
                    this.deselect();
                else
                    this.select();
            }
        }
    }
}
